.Logo{
    -ms-flex: 0.25 1;
        flex: 0.25 1;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.DiceContainer{
    -ms-flex: 1 1;
        flex: 1 1;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.Dice1{
    width: 7.5rem;
    height: 7.5rem;
    -webkit-transform: rotate(-15deg);
        -ms-transform: rotate(-15deg);
            transform: rotate(-15deg);
}

.Dice2{
    width: 7.5rem;
    height: 7.5rem;
    -webkit-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
            transform: rotate(45deg);
}

.BottomStyling{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}

.hrOne{
    border: 0;
    width: 45%;
    height: 2.5px;
    background-image: -webkit-gradient(linear, 
        left top, right top, 
        from(rgba(0, 0, 0, 0)),
        to(rgba(0, 0, 255, 0.75))
    );
    background-image: -webkit-linear-gradient(left, 
        rgba(0, 0, 0, 0),
        rgba(0, 0, 255, 0.75)
    );
    background-image: -o-linear-gradient(left, 
        rgba(0, 0, 0, 0),
        rgba(0, 0, 255, 0.75)
    );
    background-image: linear-gradient(to right, 
        rgba(0, 0, 0, 0),
        rgba(0, 0, 255, 0.75)
    );
}

.Square{
    width: 1rem;
    height: 1rem;
    background-color: var(--color-primary-dark);
    -webkit-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
            transform: rotate(45deg);
}

.hrTwo{
    border: 0;
    width: 45%;
    height: 2.5px;
    background-image: -webkit-gradient(linear,
        left top, right top,
        from(rgba(0, 0, 255, 0.75)), 
        to(rgba(0, 0, 0, 0))
    );
    background-image: -webkit-linear-gradient(left,
        rgba(0, 0, 255, 0.75), 
        rgba(0, 0, 0, 0)
    );
    background-image: -o-linear-gradient(left,
        rgba(0, 0, 255, 0.75), 
        rgba(0, 0, 0, 0)
    );
    background-image: linear-gradient(to right,
        rgba(0, 0, 255, 0.75), 
        rgba(0, 0, 0, 0)
    );
}


